<template>
    <div :class="`p${playerNumber == 1 ?'l':'r'}-5`">
        <i v-if="activePlayer == playerNumber" class="fa fa-caret-down em-2-5 "></i>
        <div :class="`p${playerNumber}`">Player {{playerNumber}}</div>
        <div :class="``">Population {{playerData[playerNumber].population}}</div>
        <div :class="``">Wealth {{playerData[playerNumber].wealth}}</div>
          <div :class="``">Income {{playerData[playerNumber].income}}</div>
    </div>
</template>
<script>
export default {
    name:"Player",
    props:{
        playerNumber:Number
    },
    computed:{
        playerData(){
            return this.$store.getters.players;
        },
        activePlayer(){
            return this.$store.getters.activePlayer;
        }
    }
}
</script>
