const faker = require('faker');

export function createGrid(gridRows) {
  const grid = {};
  for (let i = 0; i < gridRows*gridRows; i++) {
   
      grid[i] = {
        name:faker.address.streetName(),
        type:'empty',
        index: i,
        commercial: 0,
        residential: 0,
        owner: 0,
        icon: "",
        income: 0,
        population: 0
      };
    
  }
  return grid;
}

export function getNeighbouringHexIndexes(pos,offset) {
  return {
    topleft:pos - 9 + offset,
    topRight:pos - 8 + offset,
    left:pos - 1,
    right:pos + 1,
    bottomLeft:pos + 7 + offset,
    bottomRight:pos + 8 + offset
  }
}





