function createType(
    type,
    cost,
    commercialValue,
    residentialValue,
    neighbourCommercialValue,
    neighbourResidentialValue,
    icon,
    income,
    population,
    )
{
    return {
        type,
        cost,
        commercialValue,
        residentialValue,
        neighbourCommercialValue,
        neighbourResidentialValue,
        icon,
        income,
        population
    }
}

export const districtTypes = {
    industrial:createType('industrial',3,0,0,1,-1,'fa fa-industry',1,0),
    park:createType('park',6,0,0,0,1,'fa fa-tree',-1,0),
    residential:createType('residential',3,0,1,1,1,'fa fa-home',0,0),
    commercial:createType('commercial',8,0,0,0,0,'fas fa-dollar-sign',0,0)
}