import Vue from 'vue'
import Vuex from 'vuex'
import {initialState} from '../helpers/state'


Vue.use(Vuex)

export default new Vuex.Store({
  state:initialState,
  mutations: {
    setChosenPlayer(state,player){
      state.chosenPlayer = player
    },
    setActivePlayer(state){
      state.activePlayer = state.activePlayer == 1 ? 2 : 1;
    },
    setShowSlideOutMenu(state,status){
      state.showSlideOutMenu = status;
    },
    setActiveHex(state,hex){
      state.activeHex = hex;
    },
    setGridAfterHexBuild(state,data){
      let player = state.players[state.activePlayer];
      let activeHex = state.grid[data.index];
      //only update hex and player if it is bought
      if(player.wealth >= data.type.cost && activeHex.type == 'empty'){
        activeHex.icon = data.type.icon;
        activeHex.owner = state.activePlayer;
        activeHex.type = data.type.type;
        activeHex.residential += data.type.residentialValue;
        player.wealth -= data.type.cost;
        //update neighbouring hexes
        for (const position in data.neighbouringHexIndexes) {
          if(state.grid[data.neighbouringHexIndexes[position]]){
            state.grid[data.neighbouringHexIndexes[position]].residential += data.type.neighbourResidentialValue;
            state.grid[data.neighbouringHexIndexes[position]].commercial += data.type.neighbourCommercialValue;
          }
        }
      }
    },
    setGameAtTurnEnd(state){
      let incomeAfterCountingHexes = 0;
      //iterate through hexes
      for(const key in state.grid){
        //check that the hex belongs to active player
       if(state.grid[key].owner == state.activePlayer){
         //only increase population on residential hexes
          if(state.grid[key].type == 'residential'){
            //only increase population if residential value of hex is bigger than population.
              if(state.grid[key].residential > state.grid[key].population){
                state.grid[key].population += 1;
              }
              state.players[state.activePlayer].population += state.grid[key].population;
          }
          if(state.grid[key].type == 'commercial'){
            //increases income to hexes commercial value.
              if(state.grid[key].commercial > state.grid[key].income){
                state.grid[key].income = state.grid[key].commercial;
              }
          }
       
        incomeAfterCountingHexes += state.grid[key].income;
       
        }
      }
      state.players[state.activePlayer].income = incomeAfterCountingHexes;
      state.players[state.activePlayer].wealth += state.players[state.activePlayer].income;
      state.activePlayer = state.activePlayer == 1 ? 2 : 1;
    }
  },
  actions: {
  },
  modules: {
  },
  getters:{
    chosenPlayer(state){
      return state.chosenPlayer;
    },
    grid(state){
      return state.grid;
    },
    showSlideOutMenu(state){
      return state.showSlideOutMenu;
    },
    activeHex(state){
      return state.activeHex;
    },
    rows(state){
      return state.rows;
    },
    players(state){
      return state.players;
    },
    activePlayer(state){
      return state.activePlayer;
    }
  }
})
