<template>
  <div @click="closeMenu" class="board-wrapper d-flex justify-content-center">
     <ul id="grid" class="clear">
        <li :key="index" v-for="(hex,index) in grid">
          <div v-on:click="() => openMenu(hex)" :class="`hexagon hex-background-p${hex.owner} d-flex justify-content-center align-items-center`">
            <div class="hex-details-wrapper position-relative d-flex justify-content-center align-items-center" >
              <div v-if="activeHex.index == index" class="position-absolute active-marker"> </div>
           
              <div v-if="hex.owner" :class="`hex-details black position-absolute`">
                <i :class="hex.icon"></i>
                <div class="em-0-8">res: {{hex.residential}}</div>
                <div class="em-0-8">com: {{hex.commercial}}</div>
              </div>
             </div>
          </div>
        </li>
     </ul>
  </div>
</template>

<script>
export default {
  name: 'Board',
  data(){
    return {
      read:'readit'
    }
  },
  methods:{
    openMenu(hex){
      this.$store.commit('setActiveHex',hex)
      this.$store.commit('setShowSlideOutMenu',true)
    },
    closeMenu(e){
      if(!e.target.classList.contains('hexagon')){
        this.$store.commit('setShowSlideOutMenu',false)
      }
    }
  },
  computed:{
    grid(){
      return this.$store.getters.grid;
    },
    activeHex(){
      return this.$store.getters.activeHex;
    }
  },
  props: {
   
  }
}
</script>

