<template>
    <div class="border-hex-button">
        <div class="border-hex-button-inner">
            <div class="hex-button">
                <div class="hex-button-inner" @click="() => updateGrid(data.type)">
                    <i :class="data.icon + ' pr-2'"></i> {{data.price}}
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {getNeighbouringHexIndexes} from '../helpers/grid';
import {districtTypes} from '../helpers/slideMenu';
export default {
    name:'HexButton',
    props:{
       data:Object
    },
    methods:{
        updateGrid(type){
            //offet needed due to indentation of even rows
            let offset = Math.floor(this.activeHex.index / this.rows) % 2 == 0 ? 0 : 1
            let neighbouringHexIndexes = getNeighbouringHexIndexes(this.activeHex.index,offset)
            this.$store.commit('setGridAfterHexBuild',{index:this.activeHex.index,neighbouringHexIndexes,type:districtTypes[type]})
        }
    },
    computed:{
        activeHex(){
            return this.$store.getters.activeHex;
        },
        rows(){
            return this.$store.getters.rows;
        },
    }
}
</script>