<template>
  <div  @click="closeMenu" class="about">
    <div v-if="chosenPlayer != activePlayer && !showPlayerSelect" class="cover position-absolute h-100 w-100 text-right">
      <div>player {{chosenPlayer == 1 ? 2 : 1}} is moving, please wait...</div>
    </div>
    <div class="reset position-absolute p-3 text-left">
      <button @click="resetGrid">Reset Grid</button>
      <div :class="`active-player pt-3 ${vibrate ?'vibrate': ''}`">{{playerStatusMessage}}</div>
    </div>
    <div v-if="showPlayerSelect" class="player-select-cover position-absolute h-100 w-100 d-flex justify-content-center align-items-center">
      <div class="player-select">
          <div>{{playersInfo}}</div>
          <ul v-if="playersInfo != 'Sorry, This room is already full! You\'re welcome to spectate'" class="player-hex d-flex justify-content-center">
            <li :key="index" v-for="(player,index) in playersArray">
              <div :class="`d-flex align-items-center justify-content-center hexagon hex-background-p${index+1}`"  v-if="!player" @click="() =>selectPlayer(index)"> player{{index+1}} </div>
            </li>
          </ul>
      </div>
    </div>
    <SlideOutMenu/>
    <button class="mb-4" @click="endTurn">End turn</button>
    <div  class="game-wrapper d-flex justify-content-between align-items-center">
    <Player :playerNumber="1"/>
    <Board/>
    <Player :playerNumber="2"/>
    </div>
  </div>
</template>
<script>
// @ is an alias to /src
import Board from '@/components/Board.vue'
import SlideOutMenu from '@/components/SlideOutMenu.vue'
import Player from '@/components/Player.vue'
import firebase from 'firebase';
import {initialState} from '../helpers/state'

export default {
  name: 'Hexaboard',
  components: {
    Board,
    SlideOutMenu,
    Player
  },
  computed:{
    chosenPlayer(){
      return this.$store.getters.chosenPlayer;
    },
    playerStatusMessage(){
      return this.activePlayer == this.chosenPlayer ? 'Your Move': `Player ${this.activePlayer} to move`
    },
    activePlayer(){
      return this.$store.getters.activePlayer;
    },
    playersInfo(){
      let array = [];
      for(const player in this.playersArray){
        if(!this.playersArray[player]){
          array.push(player);
        }
      }
      if(array.length == 0) return 'Sorry, This room is already full! You\'re welcome to spectate';
      if(array.length == 1) return '1 player remaining';
      return 'Please choose a player';
    }
  },
  data(){
    return {
      connected_users:0,
      stop:0,
      playersArray:[],
      showPlayerSelect:true,
      vibrate:false
    }
  },
  mounted(){
    // Get players List or create it if it doesn't exist
    let playerArrayRef = firebase.database().ref('players_list')
    playerArrayRef.on('value',(snapshot) => {
      if(snapshot.val()){
        this.playersArray = snapshot.val();
      }
      else {
         playerArrayRef.set({0:false,1:false});
      }
    })
  
    //Add Listener (listens for changes in gamestate in the firebase db)
    var gameUpdate = firebase.database().ref('updateGame');
    gameUpdate.on('value', (snapshot) => {
      const data = snapshot.val();
      this.$store.replaceState({...data.state,chosenPlayer:this.chosenPlayer});
      this.$store.commit('setShowSlideOutMenu',false);
      this.vibrate = true;
      setTimeout(() => {
        this.vibrate = false;
      },520)
    });
  },
  methods:{
    resetGrid(){
      firebase.database().ref('updateGame').set({
        state:initialState
      });
    },
    selectPlayer(player){
      let playerRef = firebase.database().ref(`players_list/${player}`);
      playerRef.set(true);
      this.$store.commit('setChosenPlayer',player+1)
      this.showPlayerSelect = false;
      playerRef.onDisconnect().set(false);
    },
    closeMenu(e){
      if(e.target.classList.contains('about') || e.target.classList.contains('game-wrapper')){
         this.$store.commit('setShowSlideOutMenu',false);
      }
    },
    writeUserData() {
      firebase.database().ref('updateGame').set({
        state:this.$store.state
      });
    },
    endTurn(){
      this.$store.commit('setGameAtTurnEnd');
      this.writeUserData();
    }
  }
}
</script>
<style lang="scss" scoped>
.player-select{
  background: white;
  padding:15px;
  color:black;
  border-radius: 4px;
  box-shadow: 0px 5px 11px 0px rgb(160, 145, 145);
  border:1px solid rgb(85, 212, 235);
}

.reset {
  z-index: 12;
}

.player-select-cover{
  z-index:11;
}

.about {
  position: relative;
}

.cover {
  z-index:10;
  div{
    font-size: 22px;
    padding:10px;
  }
}

.active-player{
  font-size: 30px;
}

.vibrate{
  animation:vibrate 500ms linear;
}

$vibrationAmount:15px;
@keyframes vibrate {
  0%{
    transform: translateX(0px);
  }
   10%{
    transform: translateX(-$vibrationAmount);
  }
   20%{
    transform: translateX($vibrationAmount);
  }
   30%{
    transform: translateX($vibrationAmount);
  }
   40%{
    transform: translateX(-$vibrationAmount);
  }

  50%{
    transform:translateX(0px)
  }
   60%{
    transform: translateX(-$vibrationAmount);
  }
   70%{
    transform: translateX($vibrationAmount);
  }
   80%{
    transform: translateX($vibrationAmount);
  }
   90%{
    transform: translateX(-$vibrationAmount);
  }

  100%{
    transform:translateX(0px)
  }
}
</style>